.sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    width: 260px;
    display: block;
    z-index: 1;
    color: #fff;
    font-weight: 200;
    background-size: cover;
    background-position: center center;
    .sidebar-wrapper {
        position: relative;
        height: calc(100vh - 75px);
        overflow: auto;
        width: 260px;
        z-index: 4;
        padding-bottom: 30px;
    }
    .sidebar-background {
        position: absolute;
        z-index: 1;
        height: 100%;
        width: 100%;
        display: block;
        top: 0;
        left: 0;
        background-size: cover;
        background-position: center center;
    }
    .logo {
        padding: 10px 30px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        position: relative;
        z-index: 4;
        p {
            float: left;
            font-size: 20px;
            margin: 10px 10px;
            color: $white-color;
            line-height: 20px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        }
        a.logo-mini {
            float: left;
            text-align: center;
            width: 30px;
            margin-right: 15px;
            img {
                width: 40px;
                margin-left: -3px;
                display: block;
                margin-top: 2px;
            }
        }
        a.logo-normal {
            display: block;
        }
        .logo-img {
            width: 34px;
            display: inline-block;
            height: 34px;
            margin-left: -2px;
            margin-top: -2px;
            margin-right: 10px;
            border-radius: 30px;
            text-align: center;
        }
    }
    .logo-tim {
        border-radius: 50%;
        border: 1px solid #333;
        display: block;
        height: 61px;
        width: 61px;
        float: left;
        overflow: hidden;
        img {
            width: 60px;
            height: 60px;
        }
    }
    .nav {
        margin-top: 20px;
        li {
            >a {
                color: #FFFFFF;
                margin: 5px 15px;
                opacity: .86;
                border-radius: 4px;
            }
            &:hover>a,
            &.open>a,
            &.open>a:focus,
            &.open>a:hover {
                background: rgba(255, 255, 255, 0.13);
                opacity: 1;
            }
            &.active>a {
                color: #FFFFFF;
                opacity: 1;
                background: rgba(255, 255, 255, 0.23);
            }
        }
        p {
            margin: 0;
            line-height: 30px;
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
        }
        .caret {
            top: 24px;
            position: absolute;
            right: 15px;
        }
        i {
            font-size: 28px;
            float: left;
            margin-right: 15px;
            line-height: 30px;
            width: 30px;
            text-align: center;
        }
    }
}

.sidebar,
body>.navbar-collapse {
    .logo {
        padding: 10px 30px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        p {
            float: left;
            font-size: 20px;
            margin: 10px 10px;
            color: $white-color;
            line-height: 20px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        }
        .simple-text {
            text-transform: uppercase;
            padding: $padding-small-vertical $padding-zero;
            display: block;
            font-size: $font-size-large;
            color: $white-color;
            font-weight: $font-weight-normal;
            line-height: 30px;
        }
    }
    .logo-tim {
        border-radius: 50%;
        border: 1px solid #333;
        display: block;
        height: 61px;
        width: 61px;
        float: left;
        overflow: hidden;
        img {
            width: 60px;
            height: 60px;
        }
    }
    &:after,
    &:before {
        display: block;
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;
    }
    &:before {
        opacity: .33;
        background: #000000;
    }
    &:after {
        @include icon-gradient($black-color-top, $black-color-bottom);
        z-index: 3;
        opacity: 1;
    }
    &[data-image]:after,
    &.has-image:after {
        opacity: .77;
    }
    &[data-color="blue"]:after {
        @include icon-gradient($new-dark-blue, $blue-color-bottom);
    }
    &[data-color="azure"]:after {
        @include icon-gradient($new-blue, $azure-color-bottom);
    }
    &[data-color="green"]:after {
        @include icon-gradient($new-green, $green-color-bottom);
    }
    &[data-color="orange"]:after {
        @include icon-gradient($new-orange, $orange-color-bottom);
    }
    &[data-color="red"]:after {
        @include icon-gradient($new-red, $red-color-bottom);
    }
    &[data-color="purple"]:after {
        @include icon-gradient($new-purple, $purple-color-bottom);
    }
}

.main-panel {
    background: rgba(203, 203, 210, .15);
    position: relative;
    float: right;
    >.content {
        padding: 30px 15px;
        min-height: calc(100% - 123px);
    }
    >.footer {
        border-top: 1px solid #e7e7e7;
    }
    .navbar {
        margin-bottom: 0;
    }
}

.sidebar,
.main-panel {
    -webkit-transition-property: top, bottom;
    transition-property: top, bottom;
    -webkit-transition-duration: .2s, .2s;
    transition-duration: .2s, .2s;
    -webkit-transition-timing-function: linear, linear;
    transition-timing-function: linear, linear;
    -webkit-overflow-scrolling: touch;
}